import DepthMenu from '../../Common/DepthMenu';
import EbookTab from './Json/EbookTab.json'
import EbookItem from './Json/EbookItem.json'
import { useState } from 'react';
import { Link } from 'react-router-dom';
import './Ebook.css'

const EbookList = () => {
    const [ TabMenu, setTabMenu ] = useState("");
    const [ sortList, setSortList] = useState(EbookItem); // eslint-disable-line no-unused-vars
    const getSortList = ()=>{
        const sortcallBack = (item)=>{
            if(TabMenu === "HTML"){
                return item.Category === "HTML"
            }else if(TabMenu === "CSS"){
                return item.Category === "CSS"
            }else if(TabMenu === "Javascript"){
                return item.Category === "Javascript"
            }        
        }
        const SortList = TabMenu === '' ? sortList : sortList.filter((item)=>sortcallBack(item))
        return SortList;
    }
    return ( 
        <section className="EbookSection">
            <DepthMenu setTabMenu={setTabMenu} Data={EbookTab} FilterName={''}/>
            <article className='EbookList'>
                <div className='EbookTotalNum'> <span className='Green'>{getSortList().length}</span></div>
                {getSortList().map((Ebook)=>
                    <div className='EbookItem' key={Ebook.id}>
                        <div className='ImgWrap'>
                            <Link to={`/ebook/${Ebook.id}`} state={{Ebook : Ebook}}>
                                <img src={require(`./images/List/Book${Ebook.id}.jpg`)} alt={Ebook.BookName}/>
                            </Link>
                        </div>
                        <div className='ItemExplain'>
                            <div className='TopSection'>
                                <div className='EbookTitle'><Link to={`/ebook/${Ebook.id}`} state={{Ebook : Ebook}}>{Ebook.OurCountry && "[] "}{Ebook.BookName}</Link></div>
                                <div className='EbookSubTitle'>{Ebook.SubTitle}</div>
                            </div>
                            <div className='BottomSection'>
                                <div className='LeftSection'>
                                    <div>{Ebook.Author}<span>()</span></div>
                                    <div>{Ebook.Publisher}<span>{Ebook.PublicationDate}</span></div>
                                </div>
                                <div className='RightSection'>
                                    <Link to={`/ebook/${Ebook.id}`} state={{Ebook : Ebook}}>  </Link>
                                    <Link to={`/ebook/viewer/${Ebook.id}`} state={{Ebook : Ebook}}>E-Book </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </article>
        </section>
     );
}   
 
export default EbookList;