import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import './CommuNoticeWrite.css';
import CommuData from './CommuData';

const CommuNoticeWrite = () => {
  const { addNoticePost } = CommuData();
  const navigate = useNavigate();
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');

  const handlePostSubmit = () => {
    if (!title || !content) {
      alert('  .');
      return;
    }

    const newPost = {
      title,
      content,
      date: new Date().toLocaleDateString(),
    };

    addNoticePost(newPost);
    setTitle('');
    setContent('');
    alert('  .');
    navigate('/community');
  };
  return (
    <div className="CommuSection">
      <div>
        <div>
          <div></div>
          <div></div>
        </div>
        <ul className="CommuNav">
          <li>
            <Link to="/community"></Link>
          </li>
          <li>
            <Link to="/community/qna"></Link>
          </li>
          <li>
            <Link to="/community/faq">FAQ</Link>
          </li>
        </ul>
      </div>

      <div className="CommuRight">
        <h1>  </h1>
        <input
          className="CommuWriteTitle"
          type="text"
          placeholder=" "
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        ></input>
        <div>
          <textarea
            className="CommuWriteText"
            placeholder=" "
            value={content}
            onChange={(e) => setContent(e.target.value)}
          ></textarea>
        </div>
        <button className="CommuWriteButton" onClick={handlePostSubmit}>
        <div className="CommuWriteButtonIcon"></div>
          <div></div>
        </button>
      </div>
    </div>
  );
};

export default CommuNoticeWrite;
